	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;
}

.App-logo{
	width: 10rem;
}

.flyout {
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	min-height:100vh;
	-ms-flex-pack: justify;
